.gpt3__header {
  font-family: var(--font-family);
}
.content-image {
  display: flex;
  /* width: 667.41px;
  height: 701.59px;
  padding: 0.194px 0px 0.566px 0px; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* flex: 1; */
  max-width: 500px;
  max-height: 545px;
}
.content-image img {
  max-width: 100%;
  max-height: 100%;
}
.content .input {
  display: flex;
  justify-self: center;
}
.content .input input {
  /* width: 250px;
  height: 54px; */
  border-radius: 5px 0px 0px 5px;
  background: #052d56;
  padding: 5px;
  color: white;
}
.content .input button {
  padding: 0.5rem 1rem;
  color: #fff;
  background: #ff4820;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
}
